<template>
  <div class="mapDetails-container" v-loading="loading">
    <div class="content-box step-main">
      <el-steps :active="step" align-center style="transform: scale(1.3)">
        <el-step title="上传地图"
          ><v-icon slot="icon" name="arrow-alt-circle-up"
        /></el-step>
        <el-step title="地图启用"
          ><v-icon slot="icon" name="play-circle"
        /></el-step>
        <el-step title="完成"
          ><v-icon slot="icon" name="check-circle"
        /></el-step>
      </el-steps>
      <div class="step-title">
        <span v-if="!backPage">{{
          topologyMap.alias || topologyMap.name
        }}</span>
        <el-page-header
          @back="goBack"
          :content="topologyMap.alias || topologyMap.name"
          v-else
        >
        </el-page-header>
      </div>
    </div>
    <div class="spot-map-main">
      <div class="content-box spot-main">
        <div class="spot-filter">
          <el-input
            placeholder="ID/编号/别名"
            v-model.trim="spotFilter"
            clearable
            :maxlength="30"
          >
            <span slot="prepend">工位点</span>
          </el-input>
        </div>
        <el-table
          :data="spots"
          :height="innerHeight - 288"
          border
          highlight-current-row
          stripe
        >
          <el-table-column align="center" label="ID" fixed="left">
            <template slot-scope="scope">
              {{ scope.row.vertexPo.id }}
            </template></el-table-column
          >
          <el-table-column align="center" label="编号">
            <template slot-scope="scope">
              {{ scope.row.vertexPo.code }}
            </template></el-table-column
          >
          <el-table-column align="center" label="别名">
            <template slot-scope="scope">
              {{ scope.row.vertexPo.alias || "---" }}
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div
        class="content-box map-main"
        :style="{ height: innerHeight - 228 + 'px' }"
      >
        <div class="header">
          <span class="title">{{ topologyMap.alias || topologyMap.name }}</span>
          <span class="buttons">
            <el-switch
              v-model.trim="topologyMap.enable"
              :active-value="1"
              :inactive-value="0"
              @change="toggleEnable()"
              class="switch"
              v-hasPermi="'map/update'"
            ></el-switch>
            <a
              :href="
                topologyMap.mapFileUrl &&
                (nodeEnv === 'development' ? '/api/' : '/') +
                  topologyMap.mapFileUrl.replace(/\\/g, '/') +
                  '?t=' +
                  new Date().getTime()
              "
              :style="{ marginRight: '10px' }"
              v-hasPermi="'map/download'"
            >
              <el-button size="mini" type="primary" plain>下载</el-button>
            </a>
            <el-button
              @click="openEditDialog()"
              size="mini"
              type="warning"
              plain
              v-hasPermi="'map/update'"
              >修改</el-button
            >
            <el-button
              @click="deleteMap()"
              size="mini"
              type="danger"
              plain
              v-hasPermi="'map/delete'"
              >删除</el-button
            >
          </span>
        </div>
        <div class="detail">
          <div>地图ID: {{ topologyMap.id }}</div>
          <div>
            大小:
            {{
              topologyMap.fileSize &&
              Math.floor(topologyMap.fileSize * 1000) + "KB"
            }}
          </div>
          <!-- <div>版本: {{ topologyMap.versin || "---" }}</div> -->
          <div>更新时间: {{ topologyMap.updateTime | dateTime }}</div>
          <div>区域名称: {{ area.name }}</div>
          <!-- <div>操作人员: {{ topologyMap.userInfo || "---" }}</div> -->
        </div>
        <div class="image" ref="imageParent">
          <img :src="pngSrc" id="imageChild" />
        </div>
      </div>
    </div>
    <el-dialog
      title="编辑地图"
      :visible.sync="isDialog"
      width="410px"
      center
      @close="closeDialog"
    >
      <el-form
        label-width="85px"
        :model="mapForm"
        :rules="mapRules"
        ref="mapForm"
      >
        <el-form-item label="地图别名: ">
          <el-input
            v-model.trim="mapForm.alias"
            clearable
            placeholder="请输入地图别名"
            :maxlength="30"
          />
        </el-form-item>
        <el-form-item label="工作区域: " prop="areaId">
          <el-select
            v-model.trim="mapForm.areaId"
            placeholder="请选择区域"
            clearable
            filterable
          >
            <el-option
              v-for="(value, key) in domainList"
              :key="key"
              :label="value"
              :value="Number(key)"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="楼层: " prop="floor">
          <el-input-number
            v-model.number="mapForm.floor"
            :precision="0"
            :step="1"
            controls-position="right"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="转弯角度: " prop="determineTurningAngle">
          <el-input-number
            v-model.number="mapForm.determineTurningAngle"
            :precision="2"
            :step="1"
            controls-position="right"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="地图包: ">
          <el-button
            type="success"
            size="mini"
            @click="
              $router.push({
                path: '/mapEditor',
                query: {
                  mapId: mapForm.id,
                  fileName: mapForm.name,
                  areaId: mapForm.areaId,
                  areaName: domainList[mapForm.areaId],
                  backPage: $route.fullPath,
                },
              })
            "
            >编辑</el-button
          >
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="closeDialog">取 消</el-button>
        <el-button type="primary" @click="saveEdit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { updateMap, getMapDetails, deleteMap } from "@/api/map";
import { getDomainList } from "@/api/config/domain";
import { Message, MessageBox } from "element-ui";
export default {
  name: "mapDetails",
  props: ["innerHeight"],
  data() {
    return {
      loading: true, //等待
      spotFilter: "", //点位过滤
      Edge: [], //路径
      Vertex: [], //点位
      area: {}, //区域
      topologyMap: {}, //地图详情
      step: 1, //地图当前进度
      pngSrc: "", //png地址
      mapForm: { alias: "", reaId: "", floor: "", determineTurningAngle: "" },
      isDialog: false, //是否打开修改地图弹框
      mapRules: {
        areaId: [
          { required: true, trigger: "change", message: "请选择工作区域" },
        ],
        floor: [
          {
            message: "楼层应为数字",
            pattern: /^[-.\d]+$/,
            trigger: "blur",
          },
        ],
        determineTurningAngle: [
          {
            message: "转弯角度应为数字",
            pattern: /^[-.\d]+$/,
            trigger: "blur",
          },
        ],
      },
      domainList: {}, //工作区域列表
      backPage: "", //返回地址
      nodeEnv: process.env.NODE_ENV, //当前运行环境
    };
  },
  computed: {
    // 过滤后展示的工位点位列表
    spots() {
      return this.Vertex.filter(({ vertexPo }) => {
        var str = this.spotFilter.toLowerCase();
        return (
          (String(vertexPo.id).indexOf(str) > -1 ||
            vertexPo.alias.toLowerCase().indexOf(str) > -1 ||
            vertexPo.code.toLowerCase().indexOf(str) > -1) &&
          vertexPo.type !== 0
        );
      });
    },
  },
  mounted() {
    this.getMapDetailById();
    this.onResize();
    // 获取工作区域列表
    getDomainList().then((res) => {
      if (res.state && res.data.data) {
        res.data.data.forEach((item) => {
          this.$set(this.domainList, item.id, item.name);
        });
      }
    });
    this.backPage = this.$route.query.backPage;
  },
  methods: {
    onResize() {
      this.$nextTick(() => {
        document.getElementById("imageChild").style.maxWidth =
          this.$refs.imageParent.offsetWidth - 80 + "px";
        document.getElementById("imageChild").style.maxHeight =
          this.$refs.imageParent.offsetHeight - 50 + "px";
      });
    },
    // 获取地图详情
    getMapDetailById() {
      this.loading = true;
      getMapDetails({ mapId: this.$route.params.id }).then((res) => {
        this.loading = false;
        if (res.state) {
          let { Edge, Vertex, area, topologyMap } = res.data;
          this.Edge = Edge;
          this.Vertex = Vertex;
          this.area = area;
          this.topologyMap = topologyMap;
          this.step = topologyMap.enable ? 3 : 1;
          this.pngSrc =
            (process.env.NODE_ENV === "development" ? "/api/" : "") +
            topologyMap.backgroundImgUrl.replace(/\\/g, "/");
        }
      });
    },
    // 切换启用/禁用
    toggleEnable() {
      let msg = this.topologyMap.enable === 1 ? "启用" : "禁用";
      let fun = () => {
        this.topologyMap.enable = this.topologyMap.enable === 1 ? 0 : 1;
      };
      this.$confirm(
        `确定${msg}${this.topologyMap.alias || this.topologyMap.name}?`,
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(() => {
          updateMap(this.topologyMap).then((res) => {
            if (res.state) {
              this.$message.success(`地图${msg}成功`);
              this.getMapDetailById();
            } else {
              fun();
            }
          });
        })
        .catch(() => {
          fun();
        });
    },
    // 打开编辑地图弹框
    openEditDialog() {
      this.mapForm = JSON.parse(JSON.stringify(this.topologyMap));
      this.isDialog = true;
    },
    // 关闭弹框
    closeDialog() {
      this.$refs.mapForm.clearValidate();
      this.isDialog = false;
    },
    // 保存修改弹框
    saveEdit() {
      this.$refs.mapForm.validate((valid) => {
        if (valid) {
          updateMap(this.mapForm).then((res) => {
            if (res.state) {
              Message.success("地图修改成功");
              this.isDialog = false;
              this.getMapDetailById();
            }
          });
        }
      });
    },
    // 删除地图
    deleteMap() {
      if (this.topologyMap.enable) return Message.warning("请先禁用该地图");
      MessageBox.confirm(
        `确定删除地图: ${this.topologyMap.alias || this.topologyMap.name}?`,
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }
      )
        .then(() => {
          deleteMap(this.topologyMap.id).then((res) => {
            if (res.state) {
              Message.success("地图删除成功");
              this.$router.push("/map/index");
            }
          });
        })
        .catch(() => {});
    },
    // 返回来源页面
    goBack() {
      this.$router.push(this.backPage);
    },
  },
};
</script>
