import request from '@/utils/request'

// 获取地图列表
export function getMapList(data) {
  return request.get('/map/mapList', data)
}

// 修改地图
export function updateMap(data) {
  return request.post('/map/updateMap', data)
}

// 删除地图
export function deleteMap(id) {
  return request.post('/map/delete/' + id)
}

// 获取地图详情
export function getMapDetails(data) {
  return request.get('/map/getMapDetailById', data)
}

// 获取某工作区域下的点位列表
export function getAreaVertexes(areaId) {
  return request.get('map/getAreaAllVertexes/' + areaId)
}