import request from '@/utils/request'

// 获取所有工作区域列表☑️
export function getDomainList(data) {
  return request.post('/area/list', data)
}

// 新增和修改工作区域☑️
export function editDomain(data) {
  return request.post("/area/update", data)
}

// 删除工作区域☑️
export function deleteDomain(id) {
  return request.post("/area/delete/" + id)
}

// 获取一键归巢状态列表
export function getForcedArea() {
  return request.get("rest/forcedArea/getRestForcedAreaList")
}

// 修改一键归巢状态
export function setForcedArea({ areaId, enable }) {
  return request.put(`rest/forcedArea/addOrUpdate/${areaId}/${enable}`)
}